body {
  overflow-x: hidden;
}

.main-image {
  background: url(https://images.unsplash.com/photo-1542911077-46bd18a594f6?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1132&q=80);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 100vw;
  height: 80vh;
  position: relative;
  background-attachment: fixed;
}

.filter:after {
  background-color: rgba(0, 0, 0, 0.4);
  content: '';
  display: block;
  height: 100%;
  left: 0;
  top: 0;
  position: absolute;
  width: 100%;
  z-index: 1;
}

.card-title {
  overflow: hidden;
  color: rgba(0, 0, 0, 0.85);
  font-weight: 500;
  font-size: 16px;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.home-btn{
  margin: 0px;
  padding: 0px;
  
}

.home-email{
position: absolute;
top: 20px;
right: 40px;
color: white;
z-index: 2;
}

.logout{
position: absolute;
top: 20px;
left: 40px;
color: white;
z-index: 3;
background-color: salmon;
padding: 5px;
border-radius: 5px;
font-weight: 600;
}